import { PaymentMethod, CreateOrderRequest, CreateOrderResponse, PaymentOrder } from '../types';

// ÈÖçÁΩÆÔºöÂêéÁ´Ø API Âú∞ÂùÄ
// ËØ∑Ê†πÊçÆÂÆûÈôÖÊÉÖÂÜµ‰øÆÊîπ‰∏∫‰Ω†ÁöÑÂêéÁ´Ø API Âú∞ÂùÄ
// Next.js ÁéØÂ¢ÉÂèòÈáèÈúÄË¶Å‰ª• NEXT_PUBLIC_ ÂºÄÂ§¥ÊâçËÉΩÂú®ÂÆ¢Êà∑Á´ØËÆøÈóÆ
const PAYMENT_API_BASE_URL = process.env.NEXT_PUBLIC_PAYMENT_API_URL || 'https://your-api-domain.com/api/payment';

/**
 * ÂàõÂª∫ÊîØ‰ªòËÆ¢Âçï
 * @param request ËÆ¢ÂçïËØ∑Ê±ÇÂèÇÊï∞
 * @returns ÊîØ‰ªòÂèÇÊï∞ÔºàÁî®‰∫éË∞ÉËµ∑ÊîØ‰ªòÔºâ
 */
export const createPaymentOrder = async (
  request: CreateOrderRequest
): Promise<CreateOrderResponse> => {
  try {
    const response = await fetch(`${PAYMENT_API_BASE_URL}/create`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(request),
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(error.message || 'ÂàõÂª∫ËÆ¢ÂçïÂ§±Ë¥•');
    }

    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Create payment order error:', error);
    throw error;
  }
};

/**
 * Êü•ËØ¢ËÆ¢ÂçïÁä∂ÊÄÅ
 * @param orderId ËÆ¢ÂçïID
 * @returns ËÆ¢Âçï‰ø°ÊÅØ
 */
export const queryOrderStatus = async (orderId: string): Promise<PaymentOrder> => {
  try {
    const response = await fetch(`${PAYMENT_API_BASE_URL}/query/${orderId}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    });

    if (!response.ok) {
      throw new Error('Êü•ËØ¢ËÆ¢ÂçïÂ§±Ë¥•');
    }

    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Query order status error:', error);
    throw error;
  }
};

/**
 * Ë∞ÉËµ∑ÂæÆ‰ø°ÊîØ‰ªò
 * @param params ÂæÆ‰ø°ÊîØ‰ªòÂèÇÊï∞
 * @returns Promise<void>
 */
export const launchWechatPay = (params: {
  appId: string;
  timeStamp: string;
  nonceStr: string;
  package: string;
  signType: string;
  paySign: string;
}): Promise<void> => {
  return new Promise((resolve, reject) => {
    // Ê£ÄÊü•ÊòØÂê¶Âú®ÂæÆ‰ø°ÁéØÂ¢É
    if (typeof window === 'undefined' || !(window as any).WeixinJSBridge) {
      // Â¶ÇÊûú‰∏çÂú®ÂæÆ‰ø°ÁéØÂ¢ÉÔºåÂ∞ùËØï‰ΩøÁî® JSSDK
      if ((window as any).wx) {
        // ‰ΩøÁî®ÂæÆ‰ø° JSSDK Ë∞ÉËµ∑ÊîØ‰ªò
        (window as any).wx.chooseWXPay({
          timestamp: params.timeStamp,
          nonceStr: params.nonceStr,
          package: params.package,
          signType: params.signType,
          paySign: params.paySign,
          success: () => {
            resolve();
          },
          fail: (err: any) => {
            reject(new Error(err.errMsg || 'ÊîØ‰ªòÂ§±Ë¥•'));
          },
        });
      } else {
        reject(new Error('ËØ∑Âú®ÂæÆ‰ø°‰∏≠ÊâìÂºÄ'));
      }
      return;
    }

    // ‰ΩøÁî® WeixinJSBridge Ë∞ÉËµ∑ÊîØ‰ªò
    (window as any).WeixinJSBridge.invoke(
      'getBrandWCPayRequest',
      {
        appId: params.appId,
        timeStamp: params.timeStamp,
        nonceStr: params.nonceStr,
        package: params.package,
        signType: params.signType,
        paySign: params.paySign,
      },
      (res: any) => {
        if (res.err_msg === 'get_brand_wcpay_request:ok') {
          resolve();
        } else {
          reject(new Error(res.err_msg || 'ÊîØ‰ªòÂ§±Ë¥•'));
        }
      }
    );
  });
};

/**
 * Ë∞ÉËµ∑ÊîØ‰ªòÂÆùÊîØ‰ªò
 * @param orderString ÊîØ‰ªòÂÆùËÆ¢ÂçïÂ≠óÁ¨¶‰∏≤
 * @returns Promise<void>
 */
export const launchAlipay = (orderString: string): Promise<void> => {
  return new Promise((resolve, reject) => {
    // ÂàõÂª∫ÈöêËóèÁöÑË°®ÂçïÂπ∂Êèê‰∫§
    const form = document.createElement('form');
    form.method = 'POST';
    form.action = 'https://openapi.alipay.com/gateway.do';
    form.style.display = 'none';

    // Ëß£Êûê orderString Âπ∂ÂàõÂª∫Ë°®ÂçïÂ≠óÊÆµ
    // orderString Ê†ºÂºèÈÄöÂ∏∏ÊòØÔºöapp_id=xxx&biz_content=xxx&charset=xxx&...
    const params = new URLSearchParams(orderString);
    params.forEach((value, key) => {
      const input = document.createElement('input');
      input.type = 'hidden';
      input.name = key;
      input.value = value;
      form.appendChild(input);
    });

    document.body.appendChild(form);
    form.submit();

    // Ê≥®ÊÑèÔºöÊîØ‰ªòÂÆùÊîØ‰ªòÂêé‰ºöÂú®Êñ∞Á™óÂè£ÊâìÂºÄÔºåÊó†Ê≥ïÁõ¥Êé•ÁõëÂê¨ÁªìÊûú
    // ÈúÄË¶ÅÈÄöËøáËΩÆËØ¢ËÆ¢ÂçïÁä∂ÊÄÅÊù•Á°ÆËÆ§ÊîØ‰ªòÁªìÊûú
    resolve();
  });
};

/**
 * ËΩÆËØ¢ËÆ¢ÂçïÁä∂ÊÄÅÁõ¥Âà∞ÊîØ‰ªòÂÆåÊàêÊàñË∂ÖÊó∂
 * @param orderId ËÆ¢ÂçïID
 * @param maxAttempts ÊúÄÂ§ßÂ∞ùËØïÊ¨°Êï∞
 * @param interval ËΩÆËØ¢Èó¥ÈöîÔºàÊØ´ÁßíÔºâ
 * @returns ËÆ¢ÂçïÁä∂ÊÄÅ
 */
export const pollOrderStatus = async (
  orderId: string,
  maxAttempts = 30,
  interval = 2000
): Promise<PaymentOrder> => {
  for (let i = 0; i < maxAttempts; i++) {
    try {
      const order = await queryOrderStatus(orderId);
      if (order.status === 'SUCCESS' || order.status === 'FAILED' || order.status === 'CANCELLED') {
        return order;
      }
      // Á≠âÂæÖÂêéÁªßÁª≠ËΩÆËØ¢
      await new Promise((resolve) => setTimeout(resolve, interval));
    } catch (error) {
      console.error(`Poll attempt ${i + 1} failed:`, error);
      if (i === maxAttempts - 1) {
        throw error;
      }
      await new Promise((resolve) => setTimeout(resolve, interval));
    }
  }
  throw new Error('ÊîØ‰ªòË∂ÖÊó∂ÔºåËØ∑Á®çÂêéÊü•ËØ¢ËÆ¢ÂçïÁä∂ÊÄÅ');
};

